@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.7.2/animate.css);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#root {
  height: 100%;
}

.center {
  display: grid;
  place-items: center;
  text-align: center;
}

.parent-vertical-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #ececec;
  font-family: 'Roboto', sans-serif;
  font-size: 1em;
  color: #444;
}
* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}

h5 {
  color: #444;
  margin: 0;
}

hr {
  border-top: 2px solid #8c8b8b;
}

p,
li {
  margin-top: 1mm;
  padding: 0;
  font-size: 12px;
  color: #444;
  font-style: normal;
}

/* Paging Classes */
.page {
  width: 210mm;
  min-height: 297mm;
  padding: 15mm;
  margin: 10mm auto;
  background: white;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}
.subpage {
  height: 257mm;
}

.card {
  background-color: #f5f4f4;
  display: inline-flex;
  padding: 5px;
  width: 100%;
  align-items: center;
}

.flexBox {
  display: flex;
  flex-wrap: wrap;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

@page {
  size: A4;
  margin: 0;
}
@media print {
  html,
  body {
    width: 210mm;
    height: 297mm;
  }
  .page {
    margin: 0;
    border: medium none currentColor;
    border: initial;
    border-radius: 0;
    border-radius: initial;
    width: auto;
    width: initial;
    min-height: 0;
    min-height: initial;
    box-shadow: none;
    box-shadow: initial;
    background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
    background: initial;
    page-break-after: always;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.column {
  font-size: small;
  display: flex;
  flex-wrap: wrap;
}

.column-small {
  flex: 40% 1;
}

.column-large {
  flex: 60% 1;
}

span a {
  padding: 0 3px;
}

.sudoku-row {
  display: inline-flex;
  height: 11.201%;
}

.sudoku-column {
  display: flex;
  flex-direction: column;
  width: 500px;
  height: 500px;
  cursor: pointer;
}

.cell {
  width: 11.11%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 1px solid grey;
}
.cell:hover {
  background-color: #def;
}

.locked {
  background-color: white;
  text-align: center;
  font-size: 2.5em;
}

.editable {
  background-color: white;
}

.cellSelect {
  background-color: #aed9ff;
}

.highlightCell {
  background-color: aliceblue;
}

.highlightValue {
  font-weight: bold;
  font-size: 2.5em;
}

/* for 8th column */
.rightDark {
  border-right: 3px solid #444;
}

/* for 2 and 5th column */
.rightLight {
  border-right: 2px solid #444;
}

/* for 0th column */
.leftDark {
  border-left: 3px solid #444;
}

/* for 8th column */
.bottomDark {
  border-bottom: 3px solid #444;
}

/* for 2 and 5th row */
.bottomLight {
  border-bottom: 2px solid #444;
}

/* for 0th row */
.topDark {
  border-top: 3px solid #444;
}

