h1 {
  font-size: xxx-large;
  margin: 0px;
}

h2, h3 {
  font-weight: 600;
  margin: 0px;
}

p {
  margin: 0.5em;
  color: #21a303
}

hr {
  margin-left: 0;
  margin-right: 0;
}

.invitation {
  height: 100vh;
  width: 100vw;
}

#event h1 {
  color: #bf7b00;
}

#event h3 {
  color: #bf7b00;
}

#info h2 {
  color: #21a303
}

#info h3 {
  color: #21a303
}

#info h1{
  color: #bf7b00;
}

.name h1 {
    font-family: 'Kaushan Script', cursive;
}

